<template>
  <div id="pay_box">
    <div
      v-if="$route.query.type == 'online' && status == '0'"
      style="margin:20px;background:#fff;"
    >
      <div>
        <div class="online_title">{{ payTitle }}</div>
        <div style="font-weight:bold;margin-top:10px;font-size:1rem;">¥{{ Number(price) | point }}</div>
        <img
          :src="code"
          style="width:100%;"
          alt=""
        >
        <div style="display:flex;padding:0px 40px;justify-content:space-between;">
          <img
            src="@/assets/two/zhifb.png"
            class="w50"
            alt=""
          >
          <img
            src="@/assets/two/weix.png"
            class="w50"
            alt=""
          >
        </div>
      </div>
    </div>
    <div
      v-else-if="$route.query.type == 'online' && status == '1'"
      style="background:#f6f6f6;margin-top:20%;"
    >
      <div>
        <van-icon
          name="checked"
          size="40px"
          color="#7CD862"
        />
        <div>支付完成</div>
      </div>
      <van-button class="pay_button">确定</van-button>
    </div>
    <div
      v-else-if="$route.query.type == 'pay' && currentInfo"
      style="margin:0;"
    >
      <van-field
        readonly
        style='margin-top:10px;'
        label="付款方式"
        value='对公转账'
        input-align="right"
      />
      <van-field
        readonly
        v-model='currentInfo.companyName'
        label="公司名称"
        input-align="right"
      />
      <van-field
        readonly
        v-model='currentInfo.bankAccountName'
        label="开户行"
        input-align="right"
      />
      <van-field
        readonly
        v-model='currentInfo.bankAccount'
        label="银行账号"
        input-align="right"
      />
      <van-field
        style="margin-top:10px;"
        v-model='dataForm.payerName'
        label="付款方名称"
        input-align="right"
      />
      <div class="url_box">
        <div>付款截图</div>
        <div style='display: flex;align-items: center;'>
          <div
            v-for="(item,i) in photos"
            :key="i"
            style="margin-right:2px;"
          >
            <img
              :src="item.content"
              style="width:70px;height:70px;"
            >
          </div>
          <van-uploader
            :after-read="upLoadToQiniu"
            upload-text="点击上传"
            class="picBox"
          >
          </van-uploader>
        </div>
      </div>
      <van-button
        class="pay_button"
        @click="commitApplay"
      >提交审核</van-button>
    </div>
  </div>
</template>
<script>
import {
    GetBusinessQiniuAccesskey // 七牛云
} from '@/api/api_hotel';
import * as qiniu from 'qiniu-js';
const _env = process.env || process.env_test || process.env_production;
import { PostSystemConfigBankInfo, GetBusinessHotelLockInstallQrPay, PostBusinessHotelLockInstallTransferPay, GetBusinessHotelLockInstallByaudit } from '@/api/api_equip.js';
import { Toast } from 'vant';
export default {
    data() {
        return {
            payTitle: '请扫码支付',
            currentInfo: null,
            status: '0',
            price: null,
            code: null,
            uploader: null,
            photos: [],
            dataForm: {
                auditApprovalId: this.$route.query.auditApprovalId,
                payerName: null,
                payImage: null
            }
        };
    },
    mounted() {
        if (this.$route.query.type == 'online') {
            this.timer = setInterval(this.getIsPay, 10000);
        }
    },
    beforeDestroy() {
        clearInterval(this.timer);
    },
    created() {
        this.$store.commit('setHeader', `${this.$route.query.type}` == 'online' ? '扫码付款' : '对公转账');
        if (this.$route.query.type == 'online') {
            this.getCode();
        } else if (this.$route.query.type == 'pay') {
            this.getInfo();
        }
    },
    methods: {
        getCode() {
            GetBusinessHotelLockInstallQrPay({ auditApprovalId: this.$route.query.auditApprovalId }).done((res) => {
                this.code = 'data:image/png;base64,' + res.order;
                this.price = res.data;
            });
        },
        getIsPay() {
            GetBusinessHotelLockInstallByaudit({ auditApprovalId: this.$route.query.auditApprovalId }).done((res) => {
                this.statusInfo = res.data;
                if (res.data.payment > 0) {
                    clearInterval(this.timer);
                    this.payTitle = '已支付';
                }
            });
        },
        getInfo() {
            PostSystemConfigBankInfo().done((res) => {
                this.currentInfo = res.data;
                this.getqiniu();
            });
        },
        getqiniu() {
            GetBusinessQiniuAccesskey().done((res) => {
                this.token = res.data;
            });
        },
        async upLoadToQiniu(req) {
            this.dataForm.payImage = req.content;
            this.photos.push(req);
            const config = {
                headers: { 'Content-Type': 'multipart/form-data' },
                domain: ''
            };
            let fileType = '';
            if (req.file.type ==
 'image/png') {
                fileType = 'png';
            } else {
                fileType = 'jpg';
            }
            // 上传时候的Token,可前端自己生成,安全起见后端生成！
            const token = this.token;
            const formdata = new FormData();
            formdata.append('file', req.file);
            formdata.append('token', token);
            let _this = this;
            const observer = {
                next(res) {},
                error(err) {},
                complete(res) {
                    _this.dataForm.payImage = res.key;
                }
            };
            const observable = qiniu.upload(req.file, 'hotel/install/pay/' + new Date().getTime() + '.png', token, config);
            const subscription = observable.subscribe(observer); // 上传开始
        },
        commitApplay() {
            if (!this.dataForm.payerName) {
                Toast('请输入付款方名称');
                return false;
            }
            if (!this.dataForm.payImage) {
                Toast('请添加付款截图');
                return false;
            }
            PostBusinessHotelLockInstallTransferPay(this.dataForm).done((res) => {
                console.log('门锁工单对公转账', res);
                Toast.success(res.msg);
                setTimeout(() => {
                    this.$router.go(-1);
                }, 900);
            });
        }
    }
};
</script>
<style scoped>
#pay_box {
    font-size: 0.8rem;
}
.online_title {
    background: linear-gradient(270deg, #ff7d41 0%, #ff8e5a 100%);
    line-height: 40px;
    color: #fff;
    border-radius: 5px 5px 0 0;
    text-align: center;
    letter-spacing: 0.6rem;
    text-indent: 0.6em;
}
.w50 {
    width: 50%;
    height: 45px;
}
.pay_button {
    width: 90%;
    background: #ff7d41;
    color: #fff;
    margin: 30% 20px 0;
}
.url_box {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    font-size: 14px;
    color: #646566;
    padding: 0 0 0 16px;
    background: #fff;
    align-items: center;
}
/deep/ .van-cell {
    border-bottom: 1px solid #eee;
}
</style>



